// import plugin
import React from "react";

// import style
import Col from "./style/style";

// create interface
interface ColComponentProps {
  xs?: number;
  sm?: number;
  md?: number;
  lg?: number;
  xl?: number;
  xxl?: number;
  style?: {};
  className?: string;
  children?: JSX.Element | JSX.Element[] | any;
}

// create new component
const ColComponent = ({ children, style, className, xs, sm, md, lg, xl, xxl }: ColComponentProps, props: any) => {
  return (
    <>
      <Col xs={xs} sm={sm} md={md} lg={lg} xl={xl} xxl={xxl} style={style} className={className} {...props}>
        {children}
      </Col>
    </>
  );
};

// export new component
export default ColComponent;
